import Vue from 'vue';

export default {
        namespaced: true,
        state: {
                product: [],
                banner: [],
                bannerMenu: [],
                poster: [],
                isInit: true
        },
        getters: {
        },
        mutations: {
                _init(state, payload){
                        state.product = payload.product;
                        state.banner = payload.banner;
                        state.bannerMenu = payload.bannerMenu;
                        state.poster = payload.poster;
                },
                _changeInitState(state, payload){
                        state.isInit = payload;
                },
                _update(state,payload){
                        state.product.push(...payload);
                }
        },
        actions: {
                init({state, commit, rootState: {$http, api}}, payload){
                        $http.post(api.index.All)
                                .then(result => {
                                        if(state.isInit){
                                                commit('_init', result);
                                                commit('_changeInitState', false);
                                        }
                                })
                },
                update({state, commit, rootState: {$http, api}}, payload){
                        return $http.post(api.index.UPDATE, {cid: payload})
                                .then(result => {
                                        commit('_update',result);
                                        return 'ok';
                                });
                },
        }
}


